/* Buttons with animations */
.wrapper {

  position: relative;
}

.wrapper a {
  position: relative;
  background: #151E26;
  /* bottom: 0; */
  color: #fff;
  height: 45px;
  line-height: 45px;
  position: absolute;
  width: 100%;
  padding: 0 0 0 20px;
  font-size: 12px;
  font-weight:700;
  /* font-family: 'Arial'; */
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-transform: uppercase;
  -webkit-transition:0.1s all ease-in;
  -o-transition:0.1s all ease-in;
  transition:0.1s all ease-in;
}
.wrapper a span{
  display: block;
  width: 8px;
  top: 0;
  bottom: 0;
  margin: auto;
  right: 10px;
  height: 10px;
  position: absolute;
}

.wrapper a span span{
  background: url(http://thomaspodgro.com/source/arrow.png);
  display: block;
  width: 8px;
  top: 0;
  bottom: 0;
  margin: auto;
  right: 0px;
  height: 10px;
  opacity: 1;
  position: absolute;
}

.wrapper a .arrow:before {
  content:"";
  background: url(http://thomaspodgro.com/source/arrow.png);
  display: block;
  width: 8px;
  top: 0;
  bottom: 0;
  margin: auto;
  right: 10px;
  height: 10px;
  position: absolute;
  opacity: 0.5;
}

.wrapper a .arrow:after {
  content:"";
  background: url(http://thomaspodgro.com/source/arrow.png);
  display: block;
  width: 8px;
  top: 0;
  bottom: 0;
  margin: auto;
  right: 20px;
  height: 10px;
  position: absolute;
  opacity: 0.3;
}

.wrapper a:hover .arrow:before{
  -webkit-animation: animate-arrow-2 1s ease-in-out infinite 0.1s;
          animation: animate-arrow-2 1s ease-in-out infinite 0.1s;
}

.wrapper a:hover .arrow:after{
  -webkit-animation: animate-arrow-3 1s ease-in-out infinite 0.2s;
          animation: animate-arrow-3 1s ease-in-out infinite 0.2s;
}

.wrapper a:hover .arrow span{
  -webkit-animation: animate-arrow-1 1s ease-in-out infinite;
          animation: animate-arrow-1 1s ease-in-out infinite;
}

@-webkit-keyframes animate-arrow-1 {
  0%{-webkit-transform:translateX(-40px);transform:translateX(-40px);opacity: 0;}
  70%,100%{-webkit-transform:translateX(0px);transform:translateX(0px);opacity: 1;}
}

@keyframes animate-arrow-1 {
  0%{-webkit-transform:translateX(-40px);transform:translateX(-40px);opacity: 0;}
  70%,100%{-webkit-transform:translateX(0px);transform:translateX(0px);opacity: 1;}
}
@-webkit-keyframes animate-arrow-2 {
  0%{-webkit-transform:translateX(-20px);transform:translateX(-20px);opacity: 0;}
  70%,100%{-webkit-transform:translateX(0px);transform:translateX(0px);opacity: 0.5;}
}
@keyframes animate-arrow-2 {
  0%{-webkit-transform:translateX(-20px);transform:translateX(-20px);opacity: 0;}
  70%,100%{-webkit-transform:translateX(0px);transform:translateX(0px);opacity: 0.5;}
}
@-webkit-keyframes animate-arrow-3 {
  0%{-webkit-transform:translateX(-10px);transform:translateX(-10px);opacity: 0;}
  70%,100%{-webkit-transform:translateX(0px);transform:translateX(0px);opacity: 0.3;}
}
@keyframes animate-arrow-3 {
  0%{-webkit-transform:translateX(-10px);transform:translateX(-10px);opacity: 0;}
  70%,100%{-webkit-transform:translateX(0px);transform:translateX(0px);opacity: 0.3;}
}

/* NavBar animations */

div.row-fluid {
		text-align: center;
        margin-top: 4em;
        font-size: 50px;
}

div.row-fluid a {
	margin-left: 0.3em;
	margin-bottom: 0.6em;
}

.link {
		cursor: pointer;
		display: inline-block;
		position: relative;
		margin-bottom: 0.5em;
}

.link.anim-destination {
	display: inline-block;
	overflow: hidden;
	padding-right: 0.5em;
}

.link.anim-destination span {
	position: relative;
	display: inline-block;
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	-o-transition: transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
}

.link.anim-destination span::before {
	position: absolute;
	white-space: nowrap;	
	top: 100%;
	content: attr(data-hover);
	font-weight: 700;
}

.link.anim-destination:hover span,
.link.anim-destination:focus span {
	-webkit-transform: translateY(-100%);
	-ms-transform: translateY(-100%);
	    transform: translateY(-100%);
}

/* Arrow at Home Animation */

.arrows {
	width: 100px;
	height: 82px;
	position: absolute;
	left: 50%;
	margin-left: -30px;
	bottom: 20px;
}

.arrows path {
	stroke: #F6F5F7;
	fill: transparent;
	stroke-width: 5px;	
	animation: arrow 2s infinite;
	-webkit-animation: arrow 2s infinite; 
}

@keyframes arrow
{
0% {opacity:0}
40% {opacity:1}
80% {opacity:0}
100% {opacity:0}
}

@-webkit-keyframes arrow /*Safari and Chrome*/
{
0% {opacity:0}
40% {opacity:1}
80% {opacity:0}
100% {opacity:0}
}

.arrows path.a1 {
	animation-delay:-1s;
	-webkit-animation-delay:-1s; /* Safari 和 Chrome */
}

.arrows path.a2 {
	animation-delay:-0.5s;
	-webkit-animation-delay:-0.5s; /* Safari 和 Chrome */
}

.arrows path.a3 {	
	animation-delay:0s;
	-webkit-animation-delay:0s; /* Safari 和 Chrome */
}
